import React from 'react'
import '../Console.css'

export default class Console extends React.Component{
    state={
        console_initial_txt:"deviner$ ",
        output_txt:"",
        console_txt:"",
        help_txt : "\n> Hep Menu\n\
        help: lists all available commands\n\
        about: displays details about me\n\
        social: displays social handles [-i instagram, -t twitter, -g GitHub, -d discord, -y youtube]\n\
        Usage of Social: social [option]\n\
        exit: To exit Console\n\
        ",
        about_txt:"\n> About: \nlorem 50 Preguntas Random (Aleatorias) para conocer a alguien. Preguntas 0. Uno nunca deja de conocer a las personas, por muy cercanas que sean en la vida. Y cuando queremos profundizar en la vida de alguien, nada mejor que tener un buen arsenal de preguntas random con las que ir sacando temas de conversaciÃ³n.\n",
        social:{
            instagram:"\nInstagram: @th3_d3v1n3r\n",
            twitter:"\nTwitter: @Th3_D3v1n3r\n",
            youtube:"\nYoutube: https://www.youtube.com/channel/UCnIu88qUKLy2kYQWd1Gf8lg\n",
            github:"\GitHub: https://github.com/The-deviner\n",
            discord:"\nDiscord: https://discord.gg/8PV4uFJ\n",
        },
        intervalId:null,
        question:"Nancy is requesting the DNS for a hostname. I am requesting the same server with Nancy's source address in the request, for a different domain, but at the same time. What am I trying to achieve? What is that term called?"

    }
    componentDidMount(){
        let intervalId = setInterval(this.scrollListener,1000);
        this.setState({intervalId:intervalId})
    }
    componentWillUnmount(){
        clearInterval(this.state.intervalId);
    }
    onChange = (e) =>{
        this.setState({console_txt:e.target.value})    
    }
    scrollListener = () =>{
        var elem = document.getElementById('console_results_div');
        elem.scrollTop = elem.scrollHeight;
        
    }
    // setInterval(updateScroll,1000);
    onKeyDown = (e) =>{
        if(e.keyCode===13){    
            const {console_txt,output_txt} = this.state
            switch(console_txt){
                case "help":
                    e.preventDefault()
                    this.setState({
                        output_txt:output_txt+this.state.help_txt,
                        console_txt:""
                    })
                    break
                case "-h":
                    e.preventDefault()
                    this.setState({
                        output_txt:output_txt+this.state.help_txt,
                        console_txt:""
                    })
                    break
                case "about":
                    e.preventDefault()
                    this.setState({
                        output_txt:output_txt+this.state.about_txt,
                        console_txt:""
                    })
                    break
                case "social -i":
                    e.preventDefault()
                    this.setState({
                        output_txt:output_txt+this.state.social.instagram,
                        console_txt:""
                    })
                    break
                case "social -t":
                    e.preventDefault()
                    this.setState({
                        output_txt:output_txt+this.state.social.twitter,
                        console_txt:""
                    })
                    break
                case "social -y":
                    e.preventDefault()
                    this.setState({
                        output_txt:output_txt+this.state.social.youtube,
                        console_txt:""
                    })
                    break
                case "social -d":
                    e.preventDefault()
                    this.setState({
                        output_txt:output_txt+this.state.social.discord,
                        console_txt:""
                    })
                    break
                case "social -g":
                    e.preventDefault()
                    this.setState({
                        output_txt:output_txt+this.state.social.github,
                        console_txt:""
                    })
                    break
                case "clear":
                    e.preventDefault()
                    this.setState({
                        output_txt:"",
                        console_txt:""
                    })
                    break
                case "exit":
                    e.preventDefault()
                    this.setState({
                        output_txt:"",
                        console_txt:""
                    })
                    this.props.onClickConsole()
                    clearInterval(this.state.intervalId)
                    break
                default:
                    e.preventDefault()
                    this.setState({
                        output_txt:output_txt+"\n> Enter valid command. For help use [-h] or [help]\n",
                        console_txt:""
                    })
            }
        }
    }

    render(){
        const {console_txt,console_initial_txt,output_txt} = this.state
        
        return(
            <div className="console_main_container" id="console_main_container">
                
                <div className="console_results_div" id="console_results_div">
                    <div className="console_banner">
                        ::::::::::::'########:'##::::'##:'########::::::::::::::::::::::::::::<br/>
                        ::::::::::::... ##..:: ##:::: ##: ##.....:::::::::::::::::::::::::::::<br/>
                        ::::::::::::::: ##:::: ##:::: ##: ##::::::::::::::::::::::::::::::::::<br/>
                        ::::::::::::::: ##:::: #########: ######::::::::::::::::::::::::::::::<br/>
                        ::::::::::::::: ##:::: ##.... ##: ##...:::::::::::::::::::::::::::::::<br/>
                        ::::::::::::::: ##:::: ##:::: ##: ##::::::::::::::::::::::::::::::::::<br/>
                        ::::::::::::::: ##:::: ##:::: ##: ########::::::::::::::::::::::::::::<br/>
                        :::::::::::::::..:::::..:::::..::........:::::::::::::::::::::::::::::<br/>
                        '########::'########:'##::::'##:'####:'##::: ##:'########:'########:: <br/>
                        ##.... ##: ##.....:: ##:::: ##:. ##:: ###:: ##: ##.....:: ##.... ##: <br/>
                        ##:::: ##: ##::::::: ##:::: ##:: ##:: ####: ##: ##::::::: ##:::: ##: <br/>
                        ##:::: ##: ######::: ##:::: ##:: ##:: ## ## ##: ######::: ########:: <br/>
                        ##:::: ##: ##...::::. ##:: ##::: ##:: ##. ####: ##...:::: ##.. ##::: <br/>
                        ##:::: ##: ##::::::::. ## ##:::: ##:: ##:. ###: ##::::::: ##::. ##:: <br/>
                        ########:: ########:::. ###::::'####: ##::. ##: ########: ##:::. ##: <br/>
                        ........:::........:::::...:::::....::..::::..::........::..:::::..::
                    </div>
                    <div>
                        <h5 style={{textAlign:'center'}}>Type help for list of available commands</h5>
                    </div>
                    <div className="main_output_result_code">
                        <pre>{output_txt}</pre>
                    </div>
                </div>
                <div className="bottom_most_div_txt_div">
                    <div>{console_initial_txt}</div>
                    <textarea rows={2} spellCheck={false} value={console_txt} onKeyDown={this.onKeyDown} onChange={this.onChange}></textarea>
                </div>
            </div>
        )
    }
}